@mixin mobile{
    @media screen and (max-width:500px) {
        @content;      
    }
}

@mixin tablet{
    @media screen and (max-width:900px) {
        @content;     
    }
}


@mixin laptop{
    @media screen and (max-width:1200px) {
        @content;     
    }
}

// $darkColor: #2f2d52;



.formContainer{
    background-color: #a7bcff;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

    .formWrapper{
        background-color: white;
        padding: 20px 60px;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        gap: 10px;
        align-items: center;
        width: 280px;

        .logo{
            color: #5d5b8d;
            font-weight: bold;
            font-size: 24px;
        }

        .title{
            color: #5d5b8d;
            font-size: 12px;
        }

        form{
            display: flex;
            flex-direction: column;
            gap: 15px;

            input{
                padding: 15px;
                border: none;
                width: 250px;
                border-bottom: 1px solid #a7bcff;
            &::placeholder{
                color: rgb(175, 175, 175);
            }
            }

            button{
                background-color: #7b96ec;
                color: white;
                padding: 10px;
                font-weight: bold;
                border: none;
                border-radius: 5px;

                cursor: pointer;

                &:hover{
                    background-color: #6c7fbe;
                }
            
            }

            label{
                display: flex;
                align-items: center;
                gap: 10px;
                color: #8da4f1;
                font-size: 12px;
                cursor: pointer;

                &:hover{
                    color: #6c7fbe;
                }

                img{
                    width: 32px;
                 
                }
            }
            
            span{
                display: flex;
                text-align: center;
            }
            
            .errorMessage{
                color: #ff3e3e;

            }
            
        }
        
        p{
            color: #5d5b8d;
            font-size: 12px;
            margin-top: 10px;
        }

    }
}


.home{
    background-color: #a7bcff;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

    .container{
        border: 1px solid white;
        border-radius: 10px;
        width: 65%;
        height: 80%;
        display: flex;
        overflow: hidden;

        @include tablet{
            width: 90%;
        }
        

        .sidebar{
            flex: 1;
            background-color: #3e3c61;
            position: relative;

            &.hiddenMobile{
                @include mobile{
                    display: none;
                }
            }

            .navbar{
                display: flex;
                align-items: center;
                background-color: #2f2d52;
                height: 50px ;
                padding: 10px;
                justify-content: space-between;
                color: #ddddf7;

                .logo{
                    font-weight: bold;
                    @include tablet{
                        display: none;
                    }
                }

                .user{
                    display: flex;
                    gap: 10px;
                    padding: 10px;

                    span{
                        text-transform: capitalize;
                        margin-top: 3px;
                    }

                    img{
                        background-color: #ddddf7;
                        height: 24px;
                        width: 24px;
                        border-radius: 50%;
                        object-fit: cover;
                    }
                    

                    .logoutButton{
                        background-color: #5d5b8d;
                        color: #ddddf7;
                        font-size: 10px;
                        border: none;
                        cursor: pointer;
                        padding: 10px;
                        font-size: 13px;
                        position: absolute;
                        bottom: 15px;
                        left: 12px;
                        border-radius: 5px;


                        &:hover{
                            background-color: #2f2d52;
                        }

                    }
                }  

            }

            .search{
                border-bottom: 1px solid gray;
                
                .searchForm{
                    padding: 10px;

                    form{

                        input{
                            background-color: transparent;
                            border: none;
                            color: white;
                            outline: none;
                            
                            &::placeholder{
                                color:lightgray;
                                
                            }
                        }
                    }
                }
            }

            .userChat{
                padding: 10px;
                display: flex;
                align-items: center;
                gap: 10px;
                color: white; 
                cursor: pointer;

                &:hover{
                    background-color: #2f2d52;
                }

                img{
                    width: 50px;
                    height: 50px;
                    border-radius: 50%;
                    object-fit: cover;
                }

                .userChatInfo{
                    span{
                        font-size: 18px;
                        font-weight: 500;
                        text-transform: capitalize;
                    }

                    p{
                        font-size: 14px;
                        color: lightgray;
                        margin: 0 0 5px 0;

                    }

        
                }
            }
        }
 
        .chat{
            flex:2;

            
            &.hiddenMobile{
                @include mobile{
                    display: none;
                }
            }


            .chatInfo{
                height: 50px;
                background-color: #5d5b8d;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 10px;
                color: lightgray;

                .backToFriends{
                    display: none;
                    font-size: 15px;
                    color: lightgray;
                    border: none;
                    padding: 5px 10px;
                    color: white;
                    background-color: #3d3a6e;
                    border-radius: 5px;
                    margin-left: 10px;
                    cursor: pointer ;
                    @include mobile{
                        display: flex !important;

                    }
                    &:hover{
                        background-color: #2f2d52;
                    }
                }
            }

            span{
                text-transform: capitalize;
                padding: 5px;
                margin-top: 3px;
            }

            .chatIcons{
                display: flex;
                gap: 10px;

                img{
                    height: 24px;
                    cursor: pointer;
                }

            }

            .messages{
                background-color: #ddddf7;
                padding: 10px;
                height: calc(100% - 160px);
                overflow: scroll;

                .message{
                    display: flex;
                    gap: 20px;
                    margin-bottom: 20px;

                    .messageInfo{
                        display: flex;
                        flex-direction: column;
                        color: gray;
                        font-weight: 300;
                        img{
                            width: 40px;
                            height: 40px;
                            border-radius: 50%;
                            object-fit: cover;
                        }
                    }

                    span{
                        font-size: 0.8rem;
                    }
                    
                    .messageContent{
                        max-width: 80%;
                        display: flex;
                        flex-direction: column;
                        gap: 10px;

                        p{
                            background-color: white;
                            padding: 10px 20px;
                            border-radius: 0px 10px 10px 10px;
                            max-width: max-content;
                        }


                        img{
                            width: 50%;
                            border-radius: 0px 10px 10px 10px;

                        }

                    }

                    &.owner{
                        flex-direction: row-reverse;
                        
                        .messageContent{
                            align-items: flex-end;

                            p{
                                background-color: #8da4f1;
                                color: white;
                                border-radius: 10px 0px 10px 10px;
                            }

                            img{
                                border-radius: 10px 0px 10px 10px;
                            }
                        }
                        }

                }
            }

            .input{ 
                height: 50px;
                background-color: white;
                padding: 10px;
                display: flex;
                align-items: center;
                justify-content: space-between; 


                input{
                    width: 100%;  
                    border: none;
                    outline: none;
                    color: #2f2d52;
                    font-size: 18px;

                    &::placeholder{
                        color: lightgray;
                    }

                }

                .send{
                    display: flex;
                    align-items: center;
                    gap: 10px;


                    img{
                        height: 24px;
                        cursor: pointer;
                    }

                    button{
                        border: none;
                        padding: 10px 15px;
                        color: white;
                        background-color: #8da4f1;
                        border-radius: 5px;
                        cursor: pointer ;

                        &:hover{
                            background-color: #6c7fbe;
                        }
                    }

                    .loadingButton{
                        border: none;
                        padding: 0px 3px;
                        color: white;
                        background-color: #868fae;
                        border-radius: 5px;

                        cursor: pointer ;
                    }
                }
                
            }
        }
    }


}